* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo img {
  height: 40px;
}

nav ul {
  list-style: none;
  display: flex;
}

nav li {
  margin-left: 20px;
  position: relative;
}

nav a {
  color: #060b35;
  text-transform: uppercase;
  padding: 10px 0;
  font-weight: 500;
  text-decoration: none;
}

nav a:after {
  content: "";
  background-color: #00acdc;
  height: 2px;
  display: none;
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
}

nav a:hover, nav a.is-active {
  color: #00acdc;
  transition: all .3s;
}

nav a:hover:after, ul > li:first-of-type > a:after {
  transition: all .3s;
  display: block;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
  margin: 33px 17px;
}

.card__title {
  text-align: left;
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-left: 17px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
  transition: all .3s;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 15px;
  margin-left: 17px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews-wrapper {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-bottom: 20px;
  margin-left: 17px;
  display: flex;
}

.card__reviews {
  text-align: right;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-info {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-bottom: 20px;
  margin-left: 17px;
  display: flex;
}

.card__text {
  text-align: left;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  text-align: right;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-bottom: 17px;
  margin-left: 17px;
}

.card__button:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}

.card__button-text {
  text-align: center;
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button-text:hover {
  color: #00acdc;
  transition: all .3s;
}

.star {
  background-image: url("star.822d561e.svg");
  border-radius: .5px;
  width: 13px;
  height: 12px;
  display: inline-block;
}

.stars--4 > .star:first-child, .stars--4 > .star:nth-child(2), .stars--4 > .star:nth-child(3), .stars--4 > .star:nth-child(4) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-template-columns: 1fr;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 1fr);
  }
}
